<template>
  <div class="hello">
    <nav-bread>
        热门商品
    </nav-bread>
    <div class="accessory-result-page accessory-page">
        <div class="container">
            <div class="filter-nav">
                <span class="sortby">Sort by:</span>
                <a href="javascript:void(0)" class="default cur">Default</a>
                <a href="javascript:void(0)" class="price">Price <svg class="icon icon-arrow-short"><use xlink:href="#icon-arrow-short"></use></svg></a>
                <a href="javascript:void(0)" class="filterby stopPop">Filter by</a>
            </div>
            <div class="accessory-result">
                <!-- filter -->
                <div class="filter stopPop" id="filter">
                    <dl class="filter-price">
                        <dt>Price:</dt>
                        <dd><a href="javascript:void(0)">All</a></dd>
                        <dd>
                            <a href="javascript:void(0)">0 - 100</a>
                        </dd>
                        <dd>
                            <a href="javascript:void(0)">100 - 500</a>
                        </dd>
                        <dd>
                            <a href="javascript:void(0)">500 - 1000</a>
                        </dd>
                        <dd>
                            <a href="javascript:void(0)">1000 - 2000</a>
                        </dd>
                    </dl>
                </div>

                <!-- search result accessories list -->
                <div class="accessory-list-wrap">
                    <div class="accessory-list col-4">
                        <ul>
                            <li v-for="(goods,index) in goodsList" :key="index">
                                <div class="pic">
                                    <a href="#"><img v-bind:src="'/static/img/'+goods.productImage" alt=""></a>
                                </div>
                                <div class="main">
                                    <div class="name">{{goods.productName}}</div>
                                    <div class="price">{{goods.salePrice | money}}</div>
                                    <div class="btn-area">
                                        <a href="javascript:;" class="btn btn--m" v-on:click="addCart(goods.productId)">加入购物车</a>
                                    </div>
                                </div>
                            </li>
                            
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    </div>
    
  </div>
</template>

<script>
import NavBread from '@/components/NavBread'
export default {
  name: 'ProjectList',
  data(){
      return {
          goodsList:[]
      }
  },
  mounted(){
      this.axios.get("/static/mock/mock-goods.json")
      .then((data) => {

        this.goodsList = data.data.data

      })
  },
  methods:{
      addCart(productId){
          this.axios.post("/api/goods/addCart",{
              productId:productId
          }).then((res)=> {
              var data = res.data
              if(data.status == "0"){
                  alert("添加购物车成功")
              }
          })
      }
  },
  components:{
    NavBread
  }
}


</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



